.notes__main--content{
  display: flex;
  flex-direction: column;
  height: 100%;
}

.notes__appbar{
  align-items: center;
  background-color: $primary;
  color: $white;
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
}

.notes__content{
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px;

  .notes__title--input,
  .notes__textarea{
    border: none;
    
    &:focus{
      outline: none;
    }
  }

  .notes__title--input{
    color: $dark-gray;
    font-size: 25px;
    font-weight: 700;
    margin-bottom: 10px;
  }

  .notes__textarea{
    color: $dark-gray;
    font-size: 20px;
    flex: 1 1 auto;
    resize: none;
  }
}

.notes__image{
  img{
    max-width: 100%;
    height: 150px;
  }
}